.Wrapper {
    background-color: #1c2032;
    height: 100vh;
    border-right: 1px solid #2e344e;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.items {
    padding: 15px 5px;
    display: flex;
    flex-direction: column;
}

.item {
    margin: 0 0 15px 0;
    cursor: pointer;
    padding: 5px 0;
    position: relative;

}

.item_link {
    color: #a4acc4;
    font-family: Nunito, sans-serif;
    font-size: 13px;
    font-weight: 500;
}

.item_link::after {
    content: "";
    color: #a4acc4;
    font-family: Nunito, sans-serif;
    font-size: 13px;
    font-weight: 500;
    position: absolute;
    top: 0;
    left: -106%;
    width: 100%;
    height: 100%;
    background-color: rgba(3, 127, 255, 0.21);
    transition: left 1s ease;
}

.item:hover .item_link::after {
    content: '';
    left: 0;
}

.item:hover .item_link {
    color: #037fff;
    transition: all 0.4s ease-out;
}

.ChangeColorOnBlue {
    background-color: #037fff;
    margin: 0 0 15px 0;
    cursor: pointer;
    padding: 5px 0;
    position: relative;
    color: white;

}

.ChangeColorOnBlue .item_link {
    color: white;
}

.ChangeColorOnBlue .item_link:after {
    display: none;
}

.ChangeColorOnBlue:hover .item_link {
    color: white;
}


.item:last-child {
    margin: 0;
}

.photo {
    width: 80%;
    margin: 0 auto;
    padding: 20px 0;
}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.Footer {
    border-top: 1px solid #2e344e;
    font-size: 12px;
    color: #a4acc4;;
    padding: 15px 0;
}

.Container {
    border-bottom: 1px solid #2e344e;
}
